.attributes-column .attributes {
	display: grid;
	grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
	row-gap: 6px;
	justify-items: center;

	width: auto;

	padding: 10%;
	padding-top: 30%;
	padding-bottom: 30%;

	background: url(../images/AbilityBack.svg);
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;
}

.attributes-column .attributes .attribute-container {
	background-repeat: no-repeat !important;
	background-size: cover !important;
	background-position: top center !important;

	width: 76px;
	height: 88px;
	padding-bottom: 5%;

	box-sizing: content-box;
	vertical-align: middle;

	text-align: center;
}

.attributes-column .attributes .str {
	background: url(../images/AbilitySTR.svg);
}

.attributes-column .attributes .dex {
	background: url(../images/AbilityDEX.svg);
}

.attributes-column .attributes .con {
	background: url(../images/AbilityCON.svg);
}

.attributes-column .attributes .int {
	background: url(../images/AbilityINT.svg);
}

.attributes-column .attributes .wis {
	background: url(../images/AbilityWIS.svg);
}

.attributes-column .attributes .cha {
	background: url(../images/AbilityCHA.svg);
}

.attributes-column .attributes .attribute-container span {
	text-align: center;
	line-height: 0;
}

.attributes-column .attributes .attribute-container span.label {
	position: relative;
	top: 12%;

	font-size: 8px;
	font-weight: bold;
}

.attributes-column .attributes .attribute-container span.modifier {
	position: relative;
	top: 26.5%;

	font-size: 36px;
	font-weight: bold;
}

.attributes-column .attributes .attribute-container span.attribute {
	position: relative;
	top: 42.5%;

	font-size: 18px;
}
